import axios from 'axios';
import React, { useContext } from 'react'
import { Bars, Oval } from 'react-loader-spinner';
import { useQuery } from 'react-query';
import { useParams } from 'react-router-dom'
import { cartContext } from '../CartContext/CartContext';
import toast from 'react-hot-toast';
import { wishContext } from '../WishContext/WishContextProvider';

export default function ProductDetails() {
   // here we use useParams hooks to get id  
    const {id}=useParams()
    // console.log(localStorage.getItem('tkn'));
    // console.log(id);

    const {addToCart,result,error}=useContext(cartContext)
    const {addToWishList}=useContext(wishContext)
    // console.log(id);
    // console.log(localStorage.getItem('tkn'));  

   async function handleAddtoCart(idd){
    const value= await addToCart(idd)
    console.log("value",value);
    if(value){
      toast.success("Product added to Cart Successfully",{duration:2000,position:"top-center"})
      
    }else{
      toast.error("Error Occurred! Try Again Later.",{duration:2000,position:"top-center"})
    }
      
    }
    
    // console.log(id);
    function getProductDetails(){
      return  axios.get('https://ecommerce.routemisr.com/api/v1/products/'+id)
    }
    
    const {data,isLoading,isError}=useQuery(`productDetails-${id}`,getProductDetails)
    // console.log(data);
    // console.log(data?.data.data);

    function handleAddToWishList(id){
     const res= addToWishList(id)
      if(res){
        toast.success("Product Added To Wishlist")
      }
      else{
        toast.error("Something went wrong please try again later.")
      }
    }
    

    if (isLoading){
        return   <div className="loading-Color vh-100 d-flex justify-content-center align-items-center bg-primary bg-opacity-50">
        <Oval
       visible={true}
       height="90"
       width="90"
       color="#4fa94d"
       ariaLabel="oval-loading"
       wrapperStyle={{}}
       wrapperClassName=""
       />
         </div>
    }
   
  return <>
  <div className="container p-5">
    <div className="row align-items-center ">
        <div className="col-md-3">
            <img className='w-100' src={data.data.data.imageCover} alt="" />
            
        </div>
        <div className="col-md-9  justify-content-center">
        <h2>{data.data.data.title}</h2>
        <p className='h4 py-3'>{data.data.data.description}</p>
        <h2>{data.data.data.price}EGP</h2>
        
        <button onClick={()=>{handleAddtoCart(id)}}  className='btn w-100 bg-main text-white my-2'>Add To Cart</button>
        <button onClick={()=>{addToWishList(id)}}  className='btn w-100 bg-main text-white'>Add To Wish List</button>
       
        </div>
    </div>
  </div>
  
  
  </>
}
