import React, { useContext, useEffect } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { authUser } from '../AuthContext/AuthContext'
import { cartContext } from '../CartContext/CartContext'
import logo from  '../images/freshcart-logo.svg'
import AllOrders from './../AllOrders/AllOrders';
import { wishContext } from '../WishContext/WishContextProvider'

export default function Navbar() {

  const {myToken,setToken}=useContext(authUser)
  const {numOfCartItems}=useContext(cartContext)
  const{numOfWishItems,getUserWishList}= useContext(wishContext)
  const navigateUser=useNavigate()


  function handleLogout(){
    localStorage.removeItem('tkn')
    setToken(undefined)
    navigateUser('/login')
  }

  useEffect( ()=>{
    getUserWishList();
  },[])
  return <>
    <nav className="navbar navbar-expand-lg bg-body-tertiary">
  <div className="container-fluid">
   {myToken?<Link className="navbar-brand" to="/home"><img src={logo} alt="" /></Link>:''}
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">

        {myToken?<>
          <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/home">Home</Link>
        </li>

        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/Cateories">Cateories</Link>
        </li>

        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/Brand">Brand</Link>
        </li>

       
        <li className="nav-item position-relative">
          <Link className="nav-link active" aria-current="page" to="/cart"><i className="fa-solid fa-cart-plus"></i></Link>
          <span className="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-success">
   {numOfCartItems? numOfCartItems : ''}

    
  </span>
        </li>

        <li className="nav-item position-relative">
          <Link className="nav-link active" aria-current="page" to="/wishlist"><i className="fa-solid fa-heart fa-xl"></i></Link>
          <span className="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-success">
   {numOfWishItems? numOfWishItems : ''}

    
  </span>
        </li>
        
        
        </>:''}

       
      

      

       
       
         
     
      </ul>

      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">


      <ul className='list-unstyled d-flex  align-items-center'>
            <li className='me-2'>
            <i className="fa-brands fa-facebook-f"></i>
            </li>

            <li className='me-2'>
            <i className="fa-brands fa-twitter"></i>
            </li>

            <li className='me-2'>
            <i className="fa-brands fa-linkedin-in"></i>
            </li>

            <li className='me-2'>
            <i className="fa-brands fa-instagram"></i>
            </li>
        </ul>


         {/* here we check what show in nav bar in case authntcated user  is logged in or not. */}
        {myToken?<>
          <li className="nav-item">
            {/* here we use role attribute to add this span jobs act as button */}
          <span onClick={handleLogout} role='button' className='nav-link'>
            Logout
          </span>
        </li>
        </>:<>
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/registration">Registration</Link>
        </li>

        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/login">Login</Link>
        </li>
        
        </>}

        

       
       
      
       
         
     
      </ul>
      
    </div>
  </div>
</nav>
  
  </>
}
