import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { Bars, Oval } from 'react-loader-spinner';
import { useQuery } from 'react-query';
import HomeSlider from '../HomeSlider/HomeSlider';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import CategorySlider from '../CategorySlider/CategorySlider';
import { Link } from 'react-router-dom';
import { cartContext } from '../CartContext/CartContext';
import toast from 'react-hot-toast';
import { wishContext } from '../WishContext/WishContextProvider';


export default function Home() {
  //1- here we use useEffect  to make an API call "getProduct() call" when the componentDidMounts.
  //2- we comment this use effect beacuse we will handle this using useQuery
  // useEffect(() => {
  //   getProduct();


  // },[])
  //here we add  a state that will store our data fetched from the server
  const{addToCart}=useContext(cartContext)
  const{addToWishList,allWishlistProduct,toast}=useContext(wishContext)

  function handleAddToCart(id){
   const value= addToCart(id)

   if(value){
    toast.success("Product added to Cart Successfully",{duration:2000,position:"top-center"})
    
  }else{
    toast.error("Error Occurred! Try Again Later.",{duration:2000,position:"top-center"})
  }


  }

  function handleAddToWishList(productId){
    const res=addToWishList(productId)
    // if(res){
      
    //   toast.success(toast)
    // }else{
    //   toast.error(toast)
    // }
  }




    //1- we comment this useState, beacuse we will handle this using useQuery
  // const [allProduct, setAllProduct] = useState(null)

  //here we create function to get  data from the server



   async function getProduct(){
    return await axios.get('https://ecommerce.routemisr.com/api/v1/products')

 
  }

  const {data,isLoading,isError,isFetching,refetch}=useQuery('getAllProducts',getProduct,{refetchOnMount:true})
  console.log(data);
  // we can distract all of this data 
  // console.log(dataWithQuery.data,dataWithQuery.error,dataWithQuery.isLoading,dataWithQuery.isError);


  // here we can check if loading 
  if(isLoading){
    return   <div className="loading-Color vh-100 d-flex justify-content-center align-items-center bg-success bg-opacity-75">
    <Oval
  visible={true}
  height="90"
  width="90"
  color="#4fa94d"
  ariaLabel="oval-loading"
  wrapperStyle={{}}
  wrapperClassName=""
  />
    </div>
 
  }

  return <>

  

    <div className="container gx-0 ">
      <div className="row">
        <div className="col-md-10">
        <div className="sliderHome">
        <HomeSlider/>
        </div>
        </div>
        <div className="col-md-2 ">
          <div><img style={{height:'250px'}} className='w-100' src={require('../images/slider-image-2.jpeg')} alt="" /></div>
          <div><img style={{height:'250px'}} className='w-100' src={require('../images/slider-image-3.jpeg')} alt="" /></div>
        </div>
        <div  className='col-md-12 my-5'>
    <Link to='/cateories'><CategorySlider/></Link>
    </div>

      </div>

    </div>
    
    <div className="container my-5 ">
      <div className="row ">
      {data.data.data.map((product,index)=>{return  <div key={index} className="col-md-3 my-2 g-3 border-2 rounded overflow-hidden ">
      {/* <i class="fa-regular fa-heart position-absolute top-50 "></i> */}
          
          <div  className="product p-2 border-2 rounded overflow-hidden">

            <img  src={product.imageCover} className='w-100' alt="" />
            


           

            <h4 className='h6 text-main' >{product.category.name}</h4>
            

          
            <Link to={`/productdetails/${product.id}`}>
            <h2 className='h3'>{product.title.split(' ').slice(0,2).join(" ")}</h2>
            </Link> 
            <div className="d-flex justify-content-between">
            {product.priceAfterDiscount?<p><span className='text-decoration-line-through'>{product.price}</span>-{product.priceAfterDiscount}</p>:<p>{product.price}</p>}
            

            <p> <span ><i style={{color:'yellowgreen'}} className='fa-solid fa-star'></i></span>{product.ratingsAverage}</p>
            </div>
          </div>
         
          
          
         <div className='d-flex mt-4'>
         <button onClick={()=>{handleAddToCart(product.id)}} className='btn btn-outline-success d-block m-auto addbtn'><i className="fa-solid fa-cart-plus"></i></button>
          <button onClick={()=>{handleAddToWishList(product.id)}} className='btn btn-outline-success d-block m-auto '><i className="fa-solid fa-heart fa-xl"></i></button>
         </div>
          
        </div> })}
       
      </div>
                                    
    </div>
  
  </>
}
