import React, { useState } from 'react'
import { useFormik } from 'formik';
import * as Yup from 'yup';
import axios from  "axios";
import { ColorRing } from 'react-loader-spinner'
import { useNavigate } from "react-router-dom";

export default function Reagistration() {
  // this state to display sucess msg to user account hass been vreated
  const [isSuccess, setIsSuccess] = useState(false)

  //this state to  show the error message from backend
  const [errorMsg, setErrorMsg] = useState(undefined)

  // this state to change  the loading status of our form.
  const [isLoading, setIsLoading] = useState(false)


  //this state  is used to navigate between pages this  hook will return a function that we can call in any function not function component only*****
  const navigate=useNavigate()
  

  const userData={
    name: "",
    email:"",
    password:"",
    rePassword:"",
    phone: ""
  }

  const mySchema=Yup.object({
    name: Yup.string().required("Name is required").min(3,"Name must be at least 3 characters").max(6, "Name can not exceed 6 characters"),
    email: Yup.string().email("Invalid Email").required("Email is required"),
    password : Yup.string().min(6,'Password must be at least 3 characters').max(10,"password can not exceed 6 characters").required('Password is required'),
    // here we use function oneof() this function the menaing check if take oneof specifiec value... but here we dont know the value beacuse this value the user write it
    // but we can pass password value using Yup.ref('password') which refers to itself...
    rePassword: Yup.string().oneOf([Yup.ref('password'), null], "Passwords must match"),
    phone: Yup.number().positive("Phone number must be positive").integer("Only integer allowed").required("Field is required"),
      
  })

  // here we can add paremeter called values to catch the value  of each input field and do whatever we want with it
      //but to link this parmeter with value we must add attribite in input called value={myFormik.values.name}
  function onSubmit(values){
    // object values is  the form data that we will receive on the server side and  then send to our database.......
    
    //console.log('Submitted');
    sendRegisterData(values)
    
  }
  // here we declare this function to  make it accessible in other functions inside of this component and this function to handle api and send data to backend but here 
  // return error because 404 because this link api is not valid , if not valid dosnt return data property ......
  
  async function sendRegisterData(validate){
    //here we set loading true here because  the data is being sent to server ayan kan hyrga3 b erroe wla sucsusfull ana mhtag amel el loading button
    setIsLoading(true)
     await axios.post('https://ecommerce.routemisr.com/api/v1/auth/signup',validate)
      
     .then(function(){
      
      setIsSuccess(true)
      setTimeout(function(){
        setIsSuccess(false)
        // here we use navigate function to navigate user to login page
      navigate('/login')
      },2000)

      
      // here we reset the value of isloading to false to stop  showing spinner after successful submission
      setIsLoading(false)
     })

     .catch(function (error) {
      
      setErrorMsg(error.response.data.message);
      setTimeout(() => {
        setErrorMsg(undefined)
      }, 2000);
      // here we reset the value of isloading to false to stop  showing spinner after invalid submission
      setIsLoading(false)

     })
      
    
  }


  // here we use vaarible because this function return object ... and this object we can link our form with useFormik function.....
  const myFormik=useFormik(
    {
      initialValues:userData,

      
      onSubmit: onSubmit,


      //this way to validate  the form input using formik library but we can use  any other way to validate as well , like yup library this library help us to 
      // validate  the form values against a schema ..........

      // validate:function(values){
      //   const hamada={};
      //   const nameRegex=/^[A-Z][a-z]{3,7}$/;
      //   const phoneRegex=/^01[0125][0-9]{8}$/;
      //   if(nameRegex.test(values.name)===false){
      //     errors.name="Name should start with a B and contain at least one uppercase letter";
      //   }
      //   if(values.email.includes('@') !== true || values.email.includes('.') !== true)
      //   errors.email='Valid Email is required';
      //   if(phoneRegex.test(values.phone)===false){
      //     errors.phone='Phone number must be in the format of 01xx0000000';
      //   }
      //   if(values.password.length < 6 || values.password.length > 12)
      //   errors.password= "Password length should be between 6 to 12"

      //   if(values.rePassword !== values.password)
      //   errors.rePassword= 'Passwords do not match'
      //   console.log(errors );
      //   return hamada;
      // }

      validationSchema:mySchema,
    }
  )


  return <>
  
  <div className='w-75 m-auto p-5'>
  {isSuccess?<div className='alert alert-success text-center'>Your Account has been create</div>:""}

  {errorMsg?<div className='alert alert-danger text-center'>{errorMsg}</div>:""}

    
    <form onSubmit={myFormik.handleSubmit}>
     
    

  <label htmlFor="name">Name:</label>
  <input onBlur={myFormik.handleBlur} onChange={myFormik.handleChange} value={myFormik.values.name} className='form-control my-2' type="text"  placeholder='Name' id="name" />
  {myFormik.errors.name && myFormik.touched.name?<div className="alert alert-danger">{myFormik.errors.name}</div>:""}

  <label htmlFor="email">Email:</label>
  <input onBlur={myFormik.handleBlur}  onChange={myFormik.handleChange} value={myFormik.values.email} className='form-control my-2' type="email" placeholder='Email' id="email" />
  {myFormik.errors.email && myFormik.touched.email?<div className="alert alert-danger">{myFormik.errors.email}</div>:""}

  <label htmlFor="password">Password:</label>
  <input onBlur={myFormik.handleBlur}  onChange={myFormik.handleChange} value={myFormik.values.password} className='form-control my-2' type="password" placeholder='Password' id="password" />
  {myFormik.errors.password && myFormik.touched.password?<div className="alert alert-danger">{myFormik.errors.password}</div>:""}

  <label htmlFor="Repassword">Repassword:</label>
  <input onBlur={myFormik.handleBlur}  onChange={myFormik.handleChange} value={myFormik.values.rePassword} className='form-control my-2' type="password" placeholder='Repassword' id="rePassword" />
  {myFormik.errors.rePassword && myFormik.touched.rePassword?<div className="alert alert-danger">{myFormik.errors.rePassword}</div>:""}

  <label htmlFor="phone">Phone Number:</label>
  <input onBlur={myFormik.handleBlur}  onChange={myFormik.handleChange} value={myFormik.values.phone} className='form-control my-2' type="tel" placeholder='Phone Number' id="phone" />
  {myFormik.errors.phone && myFormik.touched.phone?<div className="alert alert-danger">{myFormik.errors.phone}</div>:""}

  <button className='btn  text-white text-bg-danger' type='submit'>
    
    
    
    
  

    {isLoading?<ColorRing
    visible={true}
    height="35"
    width="35"
    ariaLabel="color-ring-loading"
    wrapperStyle={{}}
    wrapperClassName="color-ring-wrapper"
    colors={['#fff', '#fff', '#fff', '#fff', '#fff']}/>:"Reagistr"}
    
    
    
    
    
    </button>


    </form>

  
  </div>
  
  
  </>
}
