import React, { useContext, useEffect } from 'react'
import { wishContext } from '../WishContext/WishContextProvider'
import { cartContext } from '../CartContext/CartContext';
import toast from 'react-hot-toast';

export default function WishList() {

 const{getUserWishList,allWishlistProduct,removeProduct,numOfWishItems} =useContext(wishContext)
 const {addToCart}=useContext(cartContext)

 async function handleAddToCart(id){
 const res= await addToCart(id)
 if(res){
  toast.success('Item added to Cart',{duration:2000,position:"top-center"})
 }else{
  toast.error('Error adding item to Cart! Try again later',{duration:2000,position:"top-center"})
 }
 }

 function handleRemoveProduct(id){
  const res=removeProduct(id);

  if(res){
    toast.success("Removed from Wishlist",{duration:2000,position:"top-center"});
  }
  else{
    toast.error( "Failed to remove the product",{duration:2000,position:"top-center"});
  }
 }

 useEffect(()=>{
  getUserWishList()
  
},[])
  return<>
 {allWishlistProduct.length !==0? <div className="container">
    {allWishlistProduct.map((product,index)=>{return <div key={index} className="row align-items-center">
      <div className="col-md-2">
        <img className='w-100 my-2' src={product.imageCover} alt="" />
      </div>
      <div className="col-md-8">
        <h4>{product.title}</h4>
        <h4>{product.price}EGP</h4>
        {/* <h4>{product._id}</h4> */}
        
       
      </div>
      <div className="col-md-2 ">

      <button onClick={()=>{handleAddToCart(product._id)}} className='btn btn-outline-success mx-2'><i className="fa-solid fa-cart-shopping fa-xl"></i></button>
        <button onClick={()=>{handleRemoveProduct(product._id)}} className='btn btn-outline-danger mx-2'><i className="fa-solid fa-trash fa-xl"></i></button>

      </div>
    </div>})}
  </div>:<div className='container my-5'><h2>Your Wish List Is Empty</h2></div>}
  
  
  </>
}
