import axios from 'axios';
import { useFormik } from 'formik'
import React, { useContext, useState } from 'react'
import * as Yup from 'yup';
import { cartContext } from '../CartContext/CartContext';
import { ColorRing } from 'react-loader-spinner';
import toast from 'react-hot-toast';
import { useNavigate } from 'react-router-dom';


export default function Payment() {

    const {cardId,getUserCart,clearCart} = useContext(cartContext)
    const [isLoading, setIsLoading] = useState(false)
    const navigate=useNavigate()


   async function cashPayment(values){
    setIsLoading(true)
        const flag= await axios.post(`https://ecommerce.routemisr.com/api/v1/orders/${cardId}`,values,{headers:{token:localStorage.getItem('tkn')}})
        .then((res)=>{
            console.log('from Payment',res);
            setIsLoading(false)
            // console.log('CartId ....',cardId);
           
            toast.success('Your payment is successful!',{position:"top-center"})
            clearCart();
            setTimeout(()=>{
                navigate( "/")
            },1500)

           
            return true
        })


        .catch((err)=>{

            console.log('from Payment',err);
            setIsLoading(false)
            toast.error('Your payment is Error!',{position:"top-center"})
            
            return false
        })
        
        return flag
    }
    


    async function onlinePayment(values){
        setIsLoading(true)
        //    const flag= await axios.post(`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${cardId}`,values,{params:{'url':'http://localhost:3000'}
         //   ,headers:{token:localStorage.getItem('tkn')}})


         const flag= await axios.post(`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${cardId}`,values,{headers:{token:localStorage.getItem('tkn')},params:{url:'http://localhost:3000'}})

         

           
           
            .then((res)=>{
                console.log('from Payment',res);
             window.open(res.data.session.url, "_self"); 
    
               
                return true
            })
    
    
            .catch((err)=>{
    
                console.log('from Payment',err);
               
                
                return false
            })
            
            return flag
        }



    const shippingData={
        "details": "",
        "phone": "",
        "city": ""
    }

   const validateShippingData= Yup.object({
    details: Yup.string().required(),
        phone: Yup.string().required(),
        city: Yup.string().required()
   })

  
   
  
   

    async function handleSubmit(values){

        console.log('submited ....',values);
       
        

    //   const res =  cashPayment(values)

    onlinePayment(values)
      
      
        
   
    }

 const myFormik=useFormik({
    initialValues:shippingData,
    onSubmit:handleSubmit,
    validationSchema:validateShippingData

 })

  return <>
 <form onSubmit={myFormik.handleSubmit}>

 <div className='w-75 m-auto p-5 my-2' >

     
    <div style={{fontSize:'30px'}} className='orderDetailsHeader'>Order Detailes</div>
    <label htmlFor="details">details</label>
    <input onChange={myFormik.handleChange} onBlur={myFormik.handleBlur} value={myFormik.values.details} className='form-control my-2' type="text" id='details' placeholder='details' />
    {myFormik.errors.details && myFormik.touched.details?<div className='alert alert-danger'>{myFormik.errors.details}</div>:''}

    <label htmlFor="phone">phone</label>
    <input onChange={myFormik.handleChange} onBlur={myFormik.handleBlur}  value={myFormik.values.phone} className='form-control my-2' type="text" id='phone' placeholder='phone'/>
    {myFormik.errors.phone && myFormik.touched.phone?<div className='alert alert-danger'>{myFormik.errors.phone}</div>:''}

    <label htmlFor="city">city</label>
    <textarea onChange={myFormik.handleChange} onBlur={myFormik.handleBlur}  value={myFormik.values.city}  className='form-control my-2' type="text" id='city' placeholder='city'/>
    {myFormik.errors.city && myFormik.touched.city?<div className='alert alert-danger'>{myFormik.errors.city}</div>:''}

   <div className='d-flex align-items-center justify-content-center'>
  
    
            <button  type='submit' className='btn btn-success  mx-1'>
            
        {isLoading?<ColorRing
        visible={true}
        height="35"
        width="35"
        ariaLabel="color-ring-loading"
        wrapperStyle={{}}
        wrapperClassName="color-ring-wrapper"
        colors={['#fff', '#fff', '#fff', '#fff', '#fff']}/>:"Confirm Online Order"}    
            </button>
   </div>
  </div>
        
 </form>
  
  
  </>
}
