import React from 'react'
import 'react-router-dom'
import { RouterProvider, createBrowserRouter } from 'react-router-dom'
import Layout from './Layout/Layout'
import Home from './Home/Home'
import Reagistration from './Reagistration/Reagistration'
import Login from './Login/Login'
import NotFound from './NotFound/NotFound'
import Cateories from './Cateories/Cateories';
import Brand from './Brand/Brand';
import AuthContextProvider from './AuthContext/AuthContext'
import ProtectedRoute from './ProtectedRoute/ProtectedRoute'
import { QueryClient, QueryClientProvider } from 'react-query'  
import ProductDetails from './ProductDetails/ProductDetails'
import CartContextProvider from './CartContext/CartContext'
import { Toaster } from 'react-hot-toast'
import Cart from './Cart/Cart'
import Payment from './Payment/Payment'
import AllOrders from './AllOrders/AllOrders'
import ForgotPassword from './ForgotPassword/ForgotPassword'
import VerifyResetCode from './ForgotPassword/VerifyResetCode'
import ResetPassword from './ForgotPassword/ResetPassword'
import WishList from './WishList/WishList';
import WishContextProvider, { wishContext } from './WishContext/WishContextProvider';
import CategoryDetails from './Cateories/CategoryDetails'

const myRouter=createBrowserRouter([
  {path:'/',element:<Layout/>,children:[
    {index:true, element:<ProtectedRoute><Home/></ProtectedRoute>},
    {path:'home', element:<ProtectedRoute><Home/></ProtectedRoute>},
    {path:'registration', element:<Reagistration/>},
    {path:'login', element:<Login/>},
    {path:'*', element:<NotFound/>},
    {path:'cateories', element:<ProtectedRoute><Cateories/></ProtectedRoute>},
    {path:'brand', element:<ProtectedRoute><Brand/></ProtectedRoute>},
    {path:'productdetails/:id', element:<ProtectedRoute><ProductDetails/></ProtectedRoute>},
    {path:'cart', element:<ProtectedRoute><Cart/></ProtectedRoute>},
    {path:'payment', element:<ProtectedRoute><Payment/></ProtectedRoute>},
    {path:'allorders', element:<ProtectedRoute><AllOrders/></ProtectedRoute>},
    {path:'wishlist', element:<ProtectedRoute><WishList/></ProtectedRoute>},
    {path:'forgotpassword', element:<ForgotPassword/>},
    {path:'verifyresetcode', element:<VerifyResetCode/>},
    {path:'categorydetails/:id', element:<ProtectedRoute><CategoryDetails/></ProtectedRoute>},
    {path:'resetpassword', element:<ResetPassword/>}
  ]}
])



export default function App() {
  // handle async state 
  //******* */ we use react query to handle  async data in our app. It allows us to fetch data asynchronously and cache it for future because the defult after leave component
  // the component enter in componentWillUnMount and if render this component agin  it will be get a new response from server to render the data 
  // this library help me to save a data to reuse  it in the app. It will fetch the data once and keep it for the life of the application the same idea of context********
  //now my app inside the QueryClientProvider component if you need use this go to component you need use feture of react-query "Save data to reuse" in this case Home comp.
  // this library create hook  for that called useQuery() => cach data from api
  
  const myClient=new QueryClient()
  return <>
  {/* here we give react query config to khnow react query why we use this library  */}
  
  <QueryClientProvider client={myClient}>
  
  <AuthContextProvider>
    <CartContextProvider>
  
    <WishContextProvider>

  <RouterProvider router={myRouter}/>
 
  </WishContextProvider>
  </CartContextProvider>
  </AuthContextProvider>
  
    
</QueryClientProvider>
<Toaster/>
  
  
  </>
  
}
