import React, { useContext } from 'react'
import { cartContext } from '../CartContext/CartContext'
import { Bars, Oval } from 'react-loader-spinner';
import toast from 'react-hot-toast';
import { Link } from 'react-router-dom';

export default function Cart() {

    const{totalCartPrice,allProduct,updateCart,removeSpecificProduct,clearCart}=useContext(cartContext)


  
    async function handleUpdateCart(id,newCount){
       const res=await updateCart(id,newCount)
       if(res){
        toast.success( `Updated items in the cart!` )
       }
       else{
        toast.error(`Failed to update items in the cart!`)
       }
    }


    function handleDeleteProduct(id){
       const res= removeSpecificProduct(id)

        if (res) {
            toast.success('Item has been removed from your cart') 
        }
        else{
            toast.error('Error removing item from cart')
        }
    }

    async function handleClearCart(){
        const res= await clearCart();

        

    }


  return <>

 <div className="container">
<div className='handleCart d-flex justify-content-between align-items-center p-5'>

{allProduct.length !== 0 ?<div>
<h2 style={{color:'green'}}>Shop Cart</h2>
<h3>Total Price:{totalCartPrice} EGP</h3>
</div>:<div className='h-75'><h2 className=''>Your Cart  is Empty.</h2></div>}

<Link to='/payment'>
{allProduct.length!==0?<button className='btn btn-outline-success p-3'>check out</button>:''}

</Link>
</div>



{allProduct.map((product,index)=>{return <div key={index} className="row my-3 justify-content-center align-items-center  p-2">
    <div className="col-md-2">
        <div className="imageCart">
            <img className='w-100' src={product.product.imageCover} alt="" />
        </div>
        

    </div>

    <div className="col-md-9">
        <div className="descraption">
            <h2>{product.product.title}</h2>
            <h4 className='my-3'>{product.price}EGP</h4>
            <button onClick={()=>{handleDeleteProduct(product.product._id)}} className='btn btn-outline-danger'>Remove</button>
            {/* <h4>{product._id}</h4> */}
            {/* <h4>{product.product._id}</h4> */}
            
        </div>
        

    </div>

    <div className="col-md-1">
        <div className="inc-dec-btn d-flex ">
            <button onClick={()=>{handleUpdateCart(product.product._id,product.count + 1)}}  className='btn btn-outline-success'>+</button>
            <h4 className='mx-3' > {product.count}</h4>
            <button onClick={()=>{handleUpdateCart(product.product._id,product.count - 1)}} className='btn btn-outline-success'>-</button>
            
        </div>
        

    </div>


    
    
</div>})}

{allProduct.length!==0?<button onClick={handleClearCart} className='btn btn-outline-danger m-auto d-block my-5' >Clear Cart</button>:''}




</div>
  
  
    
  
  
  </>
}
