import React from 'react'
import { Navigate } from 'react-router-dom';


 // here we create compo. ProtectedRoute to  check if user is authenticated or not 
    // if user is authenticated, then we will navigate to dashboard page else it will redirect to login page
export default function ProtectedRoute({children}) {
   // here we check if  the token exist in local storage and its value is not null if not null navigate user to page
    if(localStorage.getItem('tkn') == undefined) {
        //if null navigate user to login page 
        return <Navigate to={'/login'}/>;
    }
  return <>
  
  {children}
  
  </>
}
