import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { createContext } from 'react'
import { authUser } from '../AuthContext/AuthContext'




export const cartContext=createContext()
export default function CartContextProvider({children}) {

    const [result, setResult] = useState()   
    const [error, setError] = useState() 

    //here we use this state to store shared data  between components.
    const [numOfCartItems, setNumOfCartItems] = useState(0)
    const [totalCartPrice, setTotalCartPrice] = useState(0)
    const [allProduct, setAllProduct] = useState([])
    const [cardId, setCardId] = useState(null)


    
    const{myToken}=useContext(authUser)

  async function addToCart(id) {
     const res= await axios.post('https://ecommerce.routemisr.com/api/v1/cart',
     

       {
        "productId":id
    },
    {
        headers: {token:localStorage.getItem('tkn')}
    },
    )
        .then((res)=>{
    

            getUserCart()
            return true;
            
           
        })
        .catch((err)=>{
            console.log('err from CartContext',err);
           return false
        })
        
        return res
    }


   async function getUserCart(){
      await   axios.get('https://ecommerce.routemisr.com/api/v1/cart',
        {headers:{token:localStorage.getItem('tkn')}})
        .then((response)=>{
            setAllProduct(response.data.data.products)
            setNumOfCartItems(response.data.numOfCartItems)
            setTotalCartPrice(response.data.data.totalCartPrice)
            console.log(response);
            setCardId(response.data.data._id)
            console.log(cardId);
            // console.log('response from cartContext',allProduct);
           })
        .catch((err)=>{ 
            console.log(err);
          })



    }

   async function updateCart(id,newCount){
      const flag= await axios.put(`https://ecommerce.routemisr.com/api/v1/cart/${id}`,
     
        
        { 'count':newCount} ,
        {headers:{token:localStorage.getItem('tkn')}}  
        

        )
        .then((res)=>{
            console.log('update response',res);

            
            // setAllProduct(res?.date.data.products);
            
            
            // setTotalCartPrice(res?.data.data.totalCartPrice);
            
            // setNumOfCartItems(res?.data.numOfCartItems);

            getUserCart()
 
            return true;
        })

        .catch((err)=>{
            console.log("update context",err);
            return false
        })
        return flag

    }

   async function removeSpecificProduct(id){
      const flag= await axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/${id}`,{ headers : {token : localStorage.getItem('tkn')}})
        .then((res)=>{
            setNumOfCartItems(res.data.numOfCartItems)
            setTotalCartPrice(res.data.data.totalCartPrice)
            setAllProduct(res.data.data.products)
            return true
        })


        .catch((err)=>{
            console.log('remove error',err);
            return false
        })
        return flag
         
    }

   async function clearCart(){
      const flag= await axios.delete(`https://ecommerce.routemisr.com/api/v1/cart`,{ headers : {token : localStorage.getItem('tkn')}})
       .then((res)=>{
        console.log('clearCart',res);
        setNumOfCartItems(0)
        setAllProduct([])
        setTotalCartPrice(0)
        return true
       })

       .catch((err)=>{
        console.log('clearCart',err);
        return false
       })

        return flag
    }   


    useEffect(()=>{
        getUserCart()
        
        
    },[myToken])

 


  
  return <cartContext.Provider value={{addToCart,totalCartPrice,allProduct,numOfCartItems,result,updateCart,removeSpecificProduct,clearCart,cardId,getUserCart}}>
  
  
  {children}
  </cartContext.Provider>
}
