

import axios from 'axios';
import { Toast } from 'bootstrap';
import React, { createContext, useEffect, useState } from 'react'
import  toast  from 'react-hot-toast';

export const wishContext=createContext();
export default function WishContextProvider({children}) {
const [allWishlistProduct, setAllWishlistProduct] = useState([])
const [numOfWishItems, setNumOfWishItems] = useState(0)


 async function addToWishList(productId) {
  const res=await  axios.post('https://ecommerce.routemisr.com/api/v1/wishlist',
    
 
 {
  "productId":productId
},{headers:{token:localStorage.getItem('tkn')}},)
    .then((res)=>{
        console.log(res,'res from wishlist context');
        
        // setToast(res.data.message)
        
        // setAllWishlistProduct(res.data.data)
        getUserWishList()
        toast.success("Product Added To Wishlist",{duration:2000,position:"top-center"})
        return true
        

    })
    .catch((err)=>{
      console.log(err,'err from wishlist context');
      toast.error("Error Occurred! Try Again Later.",{duration:2000,position:"top-center"})
      return false

  })
  return res;
  }

 async function removeProduct(id){

    const res=await  axios.delete(`https://ecommerce.routemisr.com/api/v1/wishlist/${id}`,
    
     {headers:{token:localStorage.getItem('tkn')}})

       .then((res)=>{
           console.log(res,'res from wishlist context');
           getUserWishList()
           return true
   
       })
       .catch((err)=>{
         console.log(err,'err from wishlist context');
         
         return false
   
     })


  }

  async function getUserWishList(){

    const res=await  axios.get(`https://ecommerce.routemisr.com/api/v1/wishlist`,
    
     {headers:{token:localStorage.getItem('tkn')}})
     
       .then((res)=>{
           console.log(res,'res from wishlist getUser');
           setAllWishlistProduct(res.data.data)
           setNumOfWishItems(res.data.count);
           return true
   
       })
       .catch((err)=>{
         console.log(err,'err from wishlist context');
         
         return false
   
     })


  }
 


  return <wishContext.Provider value={{addToWishList,allWishlistProduct,getUserWishList,setAllWishlistProduct,allWishlistProduct,toast,removeProduct,numOfWishItems}}>
  
  {children}
  
  </wishContext.Provider>
}
