import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Oval } from 'react-loader-spinner';
import { Link } from 'react-router-dom';

export default function Cateories() {

  // const [isLoading, setIsLoading] = useState(false)
  const [allCategory, setAllCategory] = useState([])
async function addCategories(){
  // setIsLoading(true)
  // axios.get('https://route-ecommerce.onrender.com/api/v1/subcategories')
 await axios.get(`https://ecommerce.routemisr.com/api/v1/categories`)
  .then((res)=>{
    console.log('from category',res);
    // setIsLoading(true)
    setAllCategory(res.data.data)
    console.log(allCategory);
  })

  .then((err)=>{
    console.log(err);
    // setIsLoading(true)
    

  })

 
}

useEffect(()=>{
  addCategories();
},[])

if (allCategory.length === 0){
  return   <div className="loading-Color vh-100 d-flex justify-content-center align-items-center bg-primary bg-opacity-50">
  <Oval
 visible={true}
 height="90"
 width="90"
 color="#4fa94d"
 ariaLabel="oval-loading"
 wrapperStyle={{}}
 wrapperClassName=""
 />
   </div>
}

  return <>

  <div className="container  my-3">
    <div className="row">

      {allCategory.length!==0}
      {allCategory.map((category,index)=>{return   <div key={index} className="col-md-4 ">
        <Link to={`/categorydetails/${category._id}`}>
        <div className='ineer-category product mt-4 border-2 rounded overflow-hidden'>
        <img style={{height:'300px'}} className='w-100' src={category.image} alt="" />
        <h4>{category.name}</h4>
        {/* <h4>{category._id}</h4> */}
        </div>
        </Link>
      </div>
      
      
      })}
      
    </div>
  </div>
  
  
  
  </>
}
