import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Oval } from 'react-loader-spinner'

export default function Brand() {

  const [allBrand, setAllBrand] = useState([])
  // const [isLoading, setIsLoading] = useState(false)
 async function getAllBrand(){
  const res= await axios.get('https://ecommerce.routemisr.com/api/v1/brands')
    .then((res)=>{

      // console.log(res.data.data)
      setAllBrand(res.data.data)
      // setIsLoading(true)
      return true
    })

    .catch((err)=>{
      console.log(err);
      return false
    })
    return res
  }

  useEffect(()=>{
    const res= getAllBrand();
  },[])

  if(allBrand.length === 0){
    return   <div className="loading-Color vh-100 d-flex justify-content-center align-items-center bg-primary bg-opacity-50">
<Oval
visible={true}
height="90"
width="90"
color="#4fa94d"
ariaLabel="oval-loading"
wrapperStyle={{}}
wrapperClassName=""
/>
</div>
}



  return <>
  <div className="container">
    <div className="row">
     {allBrand.map((brand,index)=>{return  <div key={index} className="col-md-3">
        <img className='w-100' src={brand.image} alt={brand.name }/>
        {/* <h4>{brand.name}</h4> */}
        {/* <h5>{brand._id}</h5> */}
      </div>})}
    </div>
  </div>
  
  
  
  </>
}
