import axios from 'axios';
import React, { useState } from 'react'
import { useQuery } from 'react-query';
import Slider from "react-slick";
import { Bars } from 'react-loader-spinner';
// import CategorySlider from './CategorySlider';

export default function CategorySlider() {

  

    async function getCategory(){
        return await axios.get('https://ecommerce.routemisr.com/api/v1/categories')
    }
    const {data,isLoading,isFetching,isError} =useQuery('allCategory',getCategory)
    console.log('categories',data?.data.data);
    

    var settings = {
        dots: true,
        infinite: true,
        speed: 500, 
        slidesToShow: 7,
        slidesToScroll: 1,
      };

      if(isLoading){
        return   <Bars
        height="100"
        width="100"
        color="#fff"
        ariaLabel="bars-loading"
        wrapperStyle={{}}
        wrapperClassName=""
        visible={true}
        />
       
        
      }
      return <>
         <Slider {...settings}>
          {data.data.data.map((category,index)=>{return < div className='CategorySlider gx-2' key={index}>
           
           {/* {category.image?<img style={{height:'150px'}} className='w-100' src={require(category.image)} alt="" />: <img style={{height:'150px'}} className='w-100' src={require('../images/slider-image-1.jpeg')} alt="" />} */}
           <img  style={{height:'200px'}} className='w-100' src={category.image} alt="" />
           
           
            <h4 className='text-center'>{category.name}</h4>
            {/* <h6>{category._id}</h6> */}
          </div>
        
         })}
          
         </Slider>
      
      </>
        
        
       
    }