import React, { useContext, useState } from 'react'
import {  useFormik } from 'formik';
import * as Yup from 'yup';
import axios from  "axios";
import { ColorRing } from 'react-loader-spinner'
import { Link, useNavigate } from "react-router-dom";
import { authUser } from '../AuthContext/AuthContext';
import logo from '../images/freshcart-logo.svg'
import AnchorLink from 'react-anchor-link-smooth-scroll'
import toast from 'react-hot-toast';

export default function ResetPassword() {
  // this state to display sucess msg to user account hass been vreated
  const [isSuccess, setIsSuccess] = useState(false)

  //this state to  show the error message from backend
  const [errorMsg, setErrorMsg] = useState(undefined)

  // this state to change  the loading status of our form.
  const [isLoading, setIsLoading] = useState(false)


  //this state  is used to navigate between pages this  hook will return a function that we can call in any function not function component only*****
  const navigate=useNavigate()

  //this state for receive sharing data from AuthContext return object cry all sharing data 
  const {myToken,setToken} = useContext(authUser);
  

  const userData={
    
    email:"",
    newPassword:"",
    
   
  }

  const mySchema=Yup.object({
    
    email: Yup.string().email("Invalid Email").required("Email is required"),
    newPassword : Yup.string().min(6,'Password must be at least 3 characters').max(10,"password can not exceed 6 characters").required('Password is required'),
    
   
      
  })


  function onSubmit(values){
    
    
  
    resetPassword(values)
    
  }
 
  
  async function resetPassword(validate){

    setIsLoading(true)
      await axios.put('https://ecommerce.routemisr.com/api/v1/auth/resetPassword',validate)
      
     .then(function(res){
      console.log(res);
      localStorage.setItem('tkn', res.data.token)
      setToken(res.data.token)
      toast.success('Your account has been updated')

      setTimeout(()=>{
        navigate('/')
      },2000)

      setIsLoading(false)
     })

     .catch(function (error) {
      
      
     console.log('error from reset password', error);
      // here we reset the value of isloading to false to stop  showing spinner after invalid submission
      setIsLoading(false)

     })
      
    
  }


  const myFormik=useFormik(
    {
      initialValues:userData,

      
      onSubmit: onSubmit,


      //this way to validate  the form input using formik library but we can use  any other way to validate as well , like yup library this library help us to 
      // validate  the form values against a schema ..........

      // validate:function(values){
      //   const hamada={};
      //   const nameRegex=/^[A-Z][a-z]{3,7}$/;
      //   const phoneRegex=/^01[0125][0-9]{8}$/;
      //   if(nameRegex.test(values.name)===false){
      //     errors.name="Name should start with a B and contain at least one uppercase letter";
      //   }
      //   if(values.email.includes('@') !== true || values.email.includes('.') !== true)
      //   errors.email='Valid Email is required';
      //   if(phoneRegex.test(values.phone)===false){
      //     errors.phone='Phone number must be in the format of 01xx0000000';
      //   }
      //   if(values.password.length < 6 || values.password.length > 12)
      //   errors.password= "Password length should be between 6 to 12"

      //   if(values.rePassword !== values.password)
      //   errors.rePassword= 'Passwords do not match'
      //   console.log(errors );
      //   return hamada;
      // }

      validationSchema:mySchema,
    }
  )


  return <>
  
    <div className="container">
      <div className="row">
      <div className="col-md-6">
          <img className='w-75 p-5' src={logo} alt="" />
          <h2 role='' className=''>Welcome in our shopping application</h2>
          
        </div>
        <div className="col-md-6">
        <div className='w-75 m-auto p-5'>
  {isSuccess?<div className='alert alert-success text-center'>Welcome back</div>:""}

  {errorMsg?<div className='alert alert-danger text-center'>{errorMsg}</div>:""}
   
    
    <form onSubmit={myFormik.handleSubmit}>
     
    


  {/* <label htmlFor="email">Email:</label> */}
  <input onBlur={myFormik.handleBlur}  onChange={myFormik.handleChange} value={myFormik.values.email} className='form-control my-2' type="email" placeholder='Email' id="email" />
  {myFormik.errors.email && myFormik.touched.email?<div className="alert alert-danger">{myFormik.errors.email}</div>:""}

  {/* <label htmlFor="password">Password:</label> */}
  <input onBlur={myFormik.handleBlur}  onChange={myFormik.handleChange} value={myFormik.values.newPassword} className='form-control my-2' type="password" placeholder='New Password' id="newPassword" />
  {myFormik.errors.newPassword && myFormik.touched.newPassword?<div className="alert alert-danger">{myFormik.errors.newPassword}</div>:""}

  
  <button className='btn btn-success w-100  text-white ' type='submit'>
    
    
     
    


    {isLoading?<ColorRing
    visible={true}
    height="35"
    width="35"
    ariaLabel="color-ring-loading"
    wrapperStyle={{}}
    wrapperClassName="color-ring-wrapper"
    colors={['#fff', '#fff', '#fff', '#fff', '#fff']}/>:"Reset Password"}
    
    
    
    
    
    </button>


    </form>
    
  
    

  
  </div>
  
        </div>

        
      </div>
    </div>
  
  </>
}
